.burger__menu--header{
    position: relative;
    width: 22px;
    height: 16px;
    cursor: pointer;
    span{
        position: absolute;
        background-color: #121212;
        width: 100%;
        height: 3px;
        left: 0;
        border-radius: 2px;
    }
    span:nth-child(2){
        top: 50%;
    }
    span:nth-child(3){
        top: 100%
    }
    @media(min-width:768px){
        display: none;
    }
}