@import url(https://fonts.googleapis.com/css?family=Roboto:100,100italic,300,300italic,regular,italic,500,500italic,700,700italic,900,900italic);
*,*::before,*::after{box-sizing:border-box;padding:0;margin:0}body{margin:0;font-family:Roboto,-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}
.burger__menu--header{position:relative;width:22px;height:16px;cursor:pointer}.burger__menu--header span{position:absolute;background-color:#121212;width:100%;height:3px;left:0;border-radius:2px}.burger__menu--header span:nth-child(2){top:50%}.burger__menu--header span:nth-child(3){top:100%}@media(min-width: 768px){.burger__menu--header{display:none}}
.location{position:relative;display:-webkit-flex;display:flex;-webkit-justify-content:flex-end;justify-content:flex-end}
.header__container{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;padding-left:16px;padding-right:16px;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}@media(min-width: 768px){.header__container{-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center}}.header__block{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center}.header__title{font-size:30px;color:#0ec261;font-weight:700}
.main{grid-area:m}
.aside{grid-area:a;background-color:#151b1f}
.slider{grid-area:s;background-color:#000}
.page{display:grid;grid-template-columns:1fr;grid-template-rows:1fr;grid-template-areas:"m";min-height:100vh}@media(min-width: 768px){.page{grid-template-columns:85px 1fr;grid-template-rows:1fr;grid-template-areas:"a m"}}@media(min-width: 1024px){.page{grid-template-columns:65px 1fr 410px;grid-template-rows:1fr;grid-template-areas:"a m s"}}
